<template>
    <span>{{city}}</span>
</template>

<script>
    export default{
        data: () => ({
            city: null
        }),
        methods: {
            getCity: function () {
                var x = window.location.origin + window.location.pathname;
                return fetch(x + './modules/getCity.php')
                    .then(function (response) {
                        return response.json();
                    })
            }
        },
        created(){
            let app = this
            this.getCity().then(function (response) {
                app.city = response.city
            }).catch(console.log());
        }
    }
</script>