<template>
    <div @change="checkBirth">
        <select class="select" v-model="pickedDay" :class="{activeSelect: pickedDay}">
            <option disabled value="">{{this.questionData.selectText[0].txt}}</option>
            <option v-for="day in days" :value="day">{{day}}</option>
        </select>
        <select class="select" v-model="pickedMonth" :class="{activeSelect: pickedMonth}">
            <option disabled value="">{{this.questionData.selectText[1].txt}}</option>
            <option v-for="month in months" :value="month">{{month}}</option>
        </select>
        <select class="select" v-model="pickedYear" :class="{activeSelect: pickedYear}">
            <option disabled value="">{{this.questionData.selectText[2].txt}}</option>
            <option v-for="year in years" :value="year">{{year}}</option>
        </select>
    </div>
</template>

<script>
    import zodiaks from '../zodiaks'

    export default {
        props: {
            questionData: {
                type: Object
            }
        },
        data: () => ({
            zodiaks,
            days: null,
            months: null,
            years: null,
            pickedMonth: '',
            pickedDay: '',
            pickedYear: '',
            pickedZodiak: null
        }),
        mounted() {
            this.days = [...this.genBirthDay(1, 31)];
            this.months = this.genBirthDay(1, 12);
            this.years = this.genBirthDay(1940, (new Date()).getFullYear() - 18).reverse();
        },
        methods: {
            genBirthDay(min, max) {
                let array = [];
                for (let i = min; i <= max; i++) {
                    if (i < 10) {
                        array.push('0' + i)
                    } else {
                        array.push(i)
                    }
                }
                return array
            },
            checkBirth() {
                let Month = parseInt(this.pickedMonth),
                    Day = parseInt(this.pickedDay),
                    Year = parseInt(this.pickedYear);

                if (Month && Day && Year) {
                    let zodiak;
                    switch (Month) {
                        case 1:
                            if (Day < 20) {
                                zodiak = 10;
                            } else {
                                zodiak = 11;
                            }
                            break;
                        case 2:
                            if (Day < 19) {
                                zodiak = 11;
                            } else {
                                zodiak = 12;
                            }
                            break;
                        case 3:
                            if (Day < 21) {
                                zodiak = 12;
                            } else {
                                zodiak = 1;
                            }
                            break;
                        case 4:
                            if (Day < 20) {
                                zodiak = 1;
                            } else {
                                zodiak = 2;
                            }
                            break;
                        case 5:
                            if (Day < 21) {
                                zodiak = 2;
                            } else {
                                zodiak = 3;
                            }
                            break;
                        case 6:
                            if (Day < 21) {
                                zodiak = 3;
                            } else {
                                zodiak = 4;
                            }
                            break;
                        case 7:
                            if (Day < 23) {
                                zodiak = 4;
                            } else {
                                zodiak = 5;
                            }
                            break;
                        case 8:
                            if (Day < 23) {
                                zodiak = 5;
                            } else {
                                zodiak = 6;
                            }
                            break;
                        case 9:
                            if (Day < 23) {
                                zodiak = 6;
                            } else {
                                zodiak = 7;
                            }
                            break;
                        case 10:
                            if (Day < 23) {
                                zodiak = 7;
                            } else {
                                zodiak = 8;
                            }
                            break;
                        case 11:
                            if (Day < 22) {
                                zodiak = 8;
                            } else {
                                zodiak = 9;
                            }
                            break;
                        case 12:
                            if (Day < 22) {
                                zodiak = 9;
                            } else {
                                zodiak = 10;
                            }
                            break;
                    }
                    this.$emit('zodiakPicker', this.zodiaks[zodiak - 1].img)
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import url('https://fonts.googleapis.com/css?family=Montserrat:500,600,700|Open+Sans:400,700|Oswald:400,700&display=swap');

    option {
        color: #1E2834;
    }

    .select {
        border: 2px solid #D0060E;
        border-radius: 30px;
        min-height: 54px;
        appearance: none;
        width: 290px;
        text-align: center;
        text-align-last: center;
        margin-bottom: 20px;
        padding: 0 20px;
        color: #D0060E;
        outline: none;
        cursor: pointer;
        font: bold 18px/22px "Open Sans";
        background: url('../assets/icon_arrow.svg') no-repeat 95% 50%, transparent;
    }

    .activeSelect {
        box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.25);
        border: none;
        color: white;
        background: url('../assets/icon-arrow-up.svg') no-repeat 95% 50%, #E0343B;
    }
</style>