<template>
  <span id="countdown">
    {{minutes<10?'0'+minutes:minutes}}:{{remainingSeconds}}:{{hundreds<10?'0'+hundreds:hundreds}}
  </span>
</template>
<script>
    export default {
        data: () => ({
            minutes: null,
            seconds: 18 * 10,
            hundreds: 10,
            remainingSeconds: null
        }),
        methods: {
            timer() {
                let l = setInterval(() => {
                    this.minutes = Math.round((this.seconds - 30) / 60);
                    this.remainingSeconds = this.seconds % 60;

                    if (this.remainingSeconds < 10) {
                        this.remainingSeconds = "0" + this.remainingSeconds;
                    }
                    if (this.hundreds === 0) {
                        if (this.seconds === 0) {
                            clearInterval(l);
                        } else {

                            this.seconds--;
                            this.hundreds = 99;
                        }
                    } else {
                        this.hundreds--;
                    }
                }, 18)
            },
        },
        mounted() {
            this.timer()
        }
    }
</script>