<template>
    <transition-group name="fade" tag="div" class="quiz">
        <div class="main" :key="qidx" v-for="(question,index) in questions" v-if="qidx === index">
            <div class="questions">
                <div class="title">{{question.title}}</div>

                <button class="question" data-id="question"
                        v-if="question.type !== 'birthDate'"
                        v-for="answer in question.answers"
                        @click="next(answer)"
                        :class="{activeButton: answer.active}">
                    <p>
                        {{answer.txt}}
                    </p>
                    <img src="../assets/yes.svg" alt="" class="no">
                    <img src="../assets/no.svg" alt="">
                </button>

                <birthDate :questionData="question"
                           v-if="question.type === 'birthDate'"
                           @zodiakPicker="getZodiak"/>

                <transition name="fade">
                    <div class="done" v-if="zodiak && question.type">
                        <div class="zodiak"><img class="zodiak__img" :src="zodiak" alt=""></div>
                        <button class="button" @click="next">
                            Mai departe
                            <img src="../assets/button-arrow.svg" alt="">
                        </button>
                    </div>
                </transition>

            </div>
        </div>
    </transition-group>
</template>

<script>
    import questions from '../questions'
    import birthDate from '../components/birthDate'

    export default {
        data: () => ({
            questions,
            disabled: false,
            qidx: 0,
            zodiak: null,
        }),
        components: {birthDate},
        methods: {
            yes(event) {
                let target = event.target;
                if (target.tagName === 'BUTTON' && target.dataset.id === 'question') {
                    target.style.background = '#D0060E';
                    target.style.color = 'white';
                    target.children[1].classList.toggle('no');
                    target.children[2].classList.add('no');
                }
                if (target.tagName === 'P' || target.tagName === 'IMG') {
                    target.offsetParent.children[1].classList.toggle('no');
                    target.offsetParent.children[2].classList.add('no');
                }

            },
            next(answer) {
                if (this.qidx === (this.questions.length - 1)) {
                    setTimeout(() => {
                        this.$parent.$emit('viewIncrement', 1)
                    }, 250)
                } else {
                    this.yes(event);
                }

                this.$emit('questionsData', this.qidx);
                answer.active = true;
                this.disabled = true;
                setTimeout(() => {
                    this.qidx++;
                    answer.active = false;
                    this.disabled = false;
                }, 250)
            },
            getZodiak(zodiak) {
                this.zodiak = zodiak
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import url('https://fonts.googleapis.com/css?family=Montserrat:500,600,700|Open+Sans:400,700|Oswald:400,700&display=swap');

    .done {
        padding: 0 15px;
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .zodiak {
        position: relative;

        &__img {
            width: 41px;
            height: auto;
        }
    }

    .question {
        font: bold 18px/22px "Open Sans";
        width: 100%;
        border: 2px solid #D0060E;
        border-radius: 40px;
        color: #D0060E;
        position: relative;
        outline: none;
        cursor: pointer;
        text-align: left;
        padding-left: 23px;
        background: white;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-between;

        &:hover {
            background: #D0060E;

            p {
                &:first-child {
                    color: white;
                }
            }
        }

        p {


            &:last-child {
                text-align: center;
                bottom: 4px;
                margin-right: 10px;
                width: 35px;
                height: 35px;
                position: absolute;
                padding: 4px;
                background: white;
                border-radius: 50px;
                right: 0;
            }
        }

        .no {
            display: none;
        }

        .yes {
            display: block;
        }

        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }

    .title {
        font: 500 23px/26px Montserrat;
        justify-content: center;
        margin: 38px 0 57px 0;
        display: flex;
        align-items: flex-end;
    }

    .gender {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }

    .main {
        position: absolute;
        left: 50%;
        width: 100%;
        padding: 0 15px;
        transform: translate(-50%, 0);
        max-width: 350px;
        overflow: hidden;
    }

    .button {
        background: #23AB07;
        outline: none;
        border-radius: 40px;
        padding: 0 21px;
        display: flex;
        justify-content: space-between;
        border: none;
        cursor: pointer;
        color: #fff;
        align-items: center;
        height: 52px;
        width: 187px;
        font: 22px/28px "Open Sans";

        &:hover {
            box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.6);
        }
    }
</style>