<template>
    <div class="app">
        <headerBar v-if="view === 0"/>
        <preview @viewIncrement="getViewIncrement" v-if="view === 0"/>
        <quiz @viewIncrement="getViewIncrement" v-if="view === 1"/>
        <loading @viewIncrement="getViewIncrement" :view="view" v-if="view === 2"/>
        <result @viewIncrement="getViewIncrement" v-if="view === 3"/>
    </div>
</template>

<script>

    import 'normalize.css'
    import './assets/styles/animation.css'
    import headerBar from './components/headerBar'
    import preview from './components/views/preview'
    import quiz from './components/views/quiz'
    import loading from './components/views/loading'
    import result from './components/views/result'

    export default {
        name: 'app',
        components: {headerBar, preview, quiz, loading, result},
        data: () => ({
            view: 0
        }),
        methods: {
            getViewIncrement(view) {
                this.view = this.view + view
            }
        }
    }
</script>

<style lang="scss">
    html, body {
        background: #E5E5E5;
    }

    * {
        box-sizing: border-box;
    }

    .app {
        min-height: 100vh;
        max-width: 382px;
        margin: 0 auto;
        background: #FFFFFF;
        position: relative;
    }

    /*.wrap {*/
    /*    padding-top: 40px;*/
    /*}*/
</style>
