<template>
    <div class="lab">
        <div class="label">
            <p>
                Horoscop personal <span>pentru 2020</span>
            </p>
        </div>
        <div class="line" :key="qIndex" v-if="!loading && !green">
            <div class="line-progress" :style="{width: progressWidth + '%'}"></div>
        </div>
        <div class="line" :key="qIndex" v-if="loading">
            <div class="line line-progress" v-if="loading" :style="{width: 100 + '%'}"></div>
        </div>
        <div class="zodiak">
            <img src="../assets/zodiac.svg" alt="">
        </div>
    </div>
</template>

<script>
    import questions from '../questions'

    export default {
        props: {
            qIndex: {
                type: Number
            },
            loading: {
                type: Boolean
            },
            green: {
                type: Boolean
            }
        },
        data: () => ({
            questions,
            progressWidth: 5,
            view: null
        }),
        name: "label.vue",
        methods: {
            progressBar() {
                let part = 100 / this.questions.length;
                this.progressWidth = this.progressWidth + part
            }
        },
        beforeUpdate() {
            this.progressBar()
        }
    }
</script>

<style lang="scss" scoped>
    @import url('https://fonts.googleapis.com/css?family=Montserrat:600,700|Open+Sans|Oswald:400,700&display=swap');

    .line {
        width: 100%;
        height: 6px;
        background: #E4E4E4;
        position: relative;
        overflow: hidden;

        &-progress {
            position: absolute;
            width: 20%;
            height: 100%;
            background: #23AB07;
        }
    }

    .label {
        background: url("../assets/back_star.jpg") no-repeat 100% 100%;
        background-size: cover;
        width: 100%;
        height: 38px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        text-transform: uppercase;

        p {
            font: bold 17px/19px Oswald;
            color: white;
            text-align: center;

            span {
                color: #FFC700;;
            }
        }
    }

    .zodiak {
        text-align: center;

        img {
            padding: 4px 16px 0;
            min-width: 100%;
        }
    }

    .z_circle {
        margin-right: 10px;
        width: 41px;
        height: 41px;

        img {
            max-width: 100%;
        }
    }
</style>