<template>
    <div class="quiz wrap">
        <labelAbout :qIndex="qIndex"/>
        <div class="questions">
            <questions @questionsData="getQuestionsData"/>
        </div>
    </div>
</template>

<script>
    import labelAbout from '../label'
    import questions from '../questions'

    export default {
        data: () => ({
            qIndex: null
        }),
        components: {labelAbout, questions},
        methods: {
            getQuestionsData(questionsData) {
                this.qIndex = questionsData
            }
        }
    }
</script>

<style lang="scss" scoped>
    .questions {
        text-align: center;
        max-width: 350px;
        margin: 0 auto;
        @media (min-width: 320px) {
            padding: 0 15px;
        }
    }
</style>