<template>
    <div class="wrap loading">
        <labelAbout :loading="true"/>
        <div class="content">
            <div class="text">Îți mulțumim!</div>
            <div class="text text--second">Acum vom procesa rezultatele</div>
            <div class="procent">{{progress}}%</div>
            <div class="list">
                <div class="list__item">
                    <loaderComponent
                            text="Analiza horoscopului personal: .............."
                            :progress="progress"
                            :start="10"
                            :done="30"
                    />
                    <loaderComponent
                            text="Corecția în funcție de zodie: ........."
                            :progress="progress"
                            :start="35"
                            :done="55"
                    />
                    <loaderComponent
                            text="Calcularea opțiunilor de răspuns: ................."
                            :progress="progress"
                            :start="60"
                            :done="70"
                    />
                    <loaderComponent
                            text="Generarea previziunilor: ........"
                            :progress="progress"
                            :start="75"
                            :done="80"
                    />
                    <loaderComponent
                            text="Salvarea rezultatului: ...................."
                            :progress="progress"
                            :start="85"
                            :done="99"
                    />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import labelAbout from '../label'
    import loaderComponent from '../loader'

    export default {
        data: () => ({
            progress: 0
        }),
        props: {
            view: {
                type: Number
            }
        },
        methods: {
            loader() {
                let l = setInterval(() => {
                    this.progress++;
                    if (this.progress === 100) {
                        clearInterval(l);
                        setTimeout(() => {
                            this.$emit('viewIncrement', 1)
                        }, 2500)
                    }
                }, 100)
            }
        },
        components: {labelAbout, loaderComponent},
        mounted() {
            this.loader();
        }
    }
</script>

<style lang="scss" scoped>
    @import url('https://fonts.googleapis.com/css?family=Montserrat:500,600,700|Open+Sans:400,700|Oswald:400,700&display=swap');

    .list {
        display: flex;
        justify-content: center;
        text-align: left;
        margin-top: 35px;

    }

    .loading {
        min-height: 100vh
    }

    .content {
        text-align: center;
        margin-top: 38px;
        padding: 0 15px;
    }

    .text {
        font: 500 23px/26px "Montserrat";

        &--second {
            font: 14px/23px "Open Sans";
            margin: 30px 0 50px 0;
            color: #D0060E;
        }
    }

    .procent {
        color: #D0060E;
        font: 500 40px/26px "Montserrat";
    }

</style>